<template>
<div class="settings-container">
  <el-card class="filter-card">
    <div slot="header" class="clearfix">
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <!-- :to="{name:'home'}"  to='/'-->
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!-- / -->
    </div>
    <el-row :gutter="300">
      <el-col :xs="20" :sm="16" :md="16" :lg="12">
        <!--  -->
        <el-form ref="user_form" :model="user" label-width="80px" :rules="formRules">
          <el-form-item label="">
            {{ user.id }}
          </el-form-item>
          <el-form-item label="">
            {{ user.mobile }}
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="user.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="intro">
            <el-input type="textarea" v-model="user.intro" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="user.email" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onUserSetting" :loading="updateProfileLoading"></el-button>
          </el-form-item>
        </el-form>
        <!-- / -->
      </el-col>
      <el-col :xs="4" :sm="8" :md="8" :lg="12">
        <!-- <el-image
        style="width: 150px; height: 150px"
        :src="user.photo"
        fit="fit"></el-image> -->
        <!--  -->
        <!-- ,pinput -->
        <!-- <p @click="$refs.file.click()"></p> -->

        <!--  label,inputidlabelfor
          
        -->
        <label for="file">
          <el-avatar
            shape="square"
            :size="150"
            fit="fit"
            :src="user.photo"
          ></el-avatar>
          <p></p>
        </label>
        <!-- ,change -->
        <input
          id="file"
          type="file"
          ref="file"
          hidden
          @change="onFileChange"
        >
      </el-col>
    </el-row>
  </el-card>
  <!-- append-to-body 
    modal-append-to-body ,DOM,
  -->
  <el-dialog
    title=""
    :visible.sync="dialogVisible"
    append-to-body
    @opened="onDialogOpend"
    @closed="onDialogClosed"
  >
    <!--  -->
    <div class="preview-image-wrap">
      <!--  -->
      <img class="preview-image" :src="previewImage" alt="" ref="preview-image">
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="onUpdatePhoto" :loading="updatePhotoLoading"> </el-button>
    </span>
  </el-dialog>
</div>
</template>

<script>
import { getUserProfile, editUserProfile, editUserPhoto } from '@/api/user'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import globalBus from '@/utils/global-bus'

export default {
  name: 'SettingsIndex',
  components: {},
  props: {},
  data () {
    return {
      user: {
        id: null,
        moblie: '',
        name: '',
        intro: '',
        email: '',
        photo: ''
      },
      formRules: {
        name: [
          { required: true, message: '', tigger: 'blur' },
          { min: 1, max: 7, message: '17', tigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', tigger: 'blur' },
          {
            // 
            validator (valid, rule, callback) {
              if (valid === '') {
                callback(new Error(''))
              }
              callback()
            }
          }
        ],
        email: [
          { required: true, message: '', tigger: 'blur' },
          { pattern: /^\w+@\w+(\.\w+)+$/, message: '', tigger: 'blur' }
        ]
      },
      dialogVisible: false, // 
      previewImage: '', // 
      cropper: '',
      updatePhotoLoading: false, // loading
      updateProfileLoading: false
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadUser()
  },
  mounted () {},
  methods: {
    loadUser () {
      getUserProfile().then(res => {
        console.log(res)
        this.user = res.data.data
      })
    },
    onUserSetting () {
      // 
      this.$refs.user_form.validate(valid => {
        if (!valid) {
          // 
          return
        }
        // 
        this.updateProfileLoading = true
        const { name, intro, email } = this.user
        editUserProfile({
          name,
          intro,
          email
        }).then(res => {
          // console.log(res)
          this.$message({
            type: 'success',
            message: ''
          })
          this.updateProfileLoading = false
          // ,
          globalBus.$emit('update-user', this.user)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
          this.updateProfileLoading = false
        })
      })
    },
    onFileChange () {
      // 
      const file = this.$refs.file
      // console.dir(file)
      // console.log(file.files[0])
      const blobDate = window.URL.createObjectURL(file.files[0])
      this.previewImage = blobDate
      // ,
      this.dialogVisible = true
      //  change
      this.$refs.file.value = ''
    },
    onDialogOpend () {
      // img 
      // : img
      // ,,
      //  DOM 
      const image = this.$refs['preview-image']

      // 1, ,
      /**
       * 
       * 1. .replace(url[, hasSameSize]) 
       * 2. ,,
       */
      // 
      // 
      // if (this.cropper) {
      //   this.cropper.replace(this.previewImage)
      //   return
      // }
      this.cropper = new Cropper(image, {
        // 
        aspectRatio: 1,
        viewMode: 1,
        dragMode: 'none', // 
        cropBoxResizable: false
        // background: false

        //  crop 
        // crop (event) {
        //   console.log(event.detail.x)
        //   console.log(event.detail.y)
        //   console.log(event.detail.width)
        //   console.log(event.detail.height)
        //   console.log(event.detail.rotate)
        //   console.log(event.detail.scaleX)
        //   console.log(event.detail.scaleY)
        // }
      })
    },
    onDialogClosed () {
      // ,
      this.cropper.destroy()
    },
    onUpdatePhoto () {
      // this.updatePhotoLoading = true
      this.cropper.getCroppedCanvas().toBlob(file => {
        this.updatePhotoLoading = true
        console.log(file)
        const fd = new FormData()
        fd.append('photo', file)
        // fd 
        editUserPhoto(fd).then(res => {
          // 
          this.dialogVisible = false
          // 
          console.log(res)
          //  bolb 
          this.user.photo = window.URL.createObjectURL(file)
          // loading
          this.updatePhotoLoading = false

          this.$message({
            type: 'success',
            message: ''
          })
          // 
          globalBus.$emit('update-user', this.user)
          // 
          // this.user.photo = res.data.data.photo
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
.preview-image-wrap {
    /* Ensure the size of the image fit the container perfectly */
  .preview-image {
    img {
      display: block;
      /* This rule is very important, please don't ignore this */
      // max-width: 100%;
      height: 200px;
    }
  }
}
