<template>
<div class="publish-container">
  <el-card class="filter-card">
    <div slot="header" class="clearfix">
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <!-- :to="{name:'home'}"  to='/'-->
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.query.id ? '' : '' }}</el-breadcrumb-item>
      </el-breadcrumb>
      <!-- / -->
    </div>
    <!--  -->
    <el-form ref="publish-form" :model="article" label-width="60px" :rules="fromRules">
      <el-form-item label="" prop="title">
        <el-input v-model="article.title"></el-input>
      </el-form-item>
      <el-form-item label="" prop="content">
        <!-- <el-input type="textarea" v-model="article.content"></el-input> -->
        <el-tiptap v-model="article.content" :extensions="extensions" height="350" placeholder=""/>
      </el-form-item>
      <el-form-item label="" prop="cover">
        <el-radio-group v-model="article.cover.type">
          <el-radio :label="1"></el-radio>
          <el-radio :label="3"></el-radio>
          <el-radio :label="0"></el-radio>
          <el-radio :label="-1"></el-radio>
        </el-radio-group>
        <!--
           article.cover.images 

          ,
          ,$event,,$event

          v-model
          ,v-model
          v-model="article.cover.images[index]"
          value
            :value="article.cover.images[index]"
          input,, = 
            @input="article.cover.images[index]" = 

          : v-model,
        -->
        <template v-if="article.cover.type > 0">
          <upload-cover
            v-for="(cover, index) in article.cover.type"
            :key="index"
            v-model="article.cover.images[index]"
          ></upload-cover>
          <!-- <upload-cover
            v-for="(cover, index) in article.cover.type"
            :key="index"
            :cover-image="article.cover.images[index]"
            @update-cover="onUpdateCover(index, $event)"
          ></upload-cover> -->
        </template>
      </el-form-item>
      <el-form-item label="" prop="channel_id">
        <el-select v-model="article.channel_id" placeholder="">
          <el-option v-for="(channel, index) in channels" :key="index" :label="channel.name" :value="channel.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onPublish(false)"></el-button>
        <el-button @click="onPublish(true)"></el-button>
      </el-form-item>
    </el-form>
  </el-card>
</div>
</template>

<script>
import UploadCover from './components/opload-cover'
import { getArticleChannels, AddArticle, getArticle, updateArticle } from '@/api/article'
// 
import {
  //  extensions
  ElementTiptap,
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  TodoItem,
  TodoList,
  HorizontalRule,
  Fullscreen,
  Preview,
  Image,
  CodeBlock
} from 'element-tiptap'
// import element-tiptap 
import 'element-tiptap/lib/index.css'
import { uploadImages } from '@/api/image'
export default {
  name: 'PublishIndex',
  components: {
    'el-tiptap': ElementTiptap,
    UploadCover
  },
  props: {},
  data () {
    return {
      article: {
        title: '',
        content: '', // 
        cover: {
          type: 1, //  -1,0,1,3,
          images: [] // 
        },
        channel_id: null
      },
      channels: [],
      //  extensions 
      // 
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(), //  ,
        new Heading({ level: 5 }), // 5
        new Bold({ bubble: true }), // 
        new Image({
          //   base64   ,
          // 
          uploadRequest (file) {
            //  Content-Type multipart/form-data FormData()
            const fd = new FormData()
            fd.append('image', file)
            // promise,promise,uploadRequest
            //  Promise, axiosPromise
            //  Promise
            return uploadImages(fd).then(res => {
              // 
              return res.data.data.url
            })
          }
        }),
        new Underline(), // 
        new Italic(), // 
        new Strike(), // 
        new HorizontalRule(), // 
        new ListItem(),
        new BulletList(), // 
        new OrderedList(), // 
        new TodoItem(),
        new TodoList(),
        new Fullscreen(), // 
        new Preview(), // 
        new CodeBlock() // 
      ],
      fromRules: {
        title: [
          { required: true, message: '', tigger: 'blur' },
          { min: 5, max: 30, message: '530', tigger: 'blur' }
        ],
        content: [
          {
            // 
            validator (rule, value, callback) {
              if (value === '<p></p>') {
                // 
                callback(new Error(''))
              } else {
                callback()
              }
            }
          },
          { required: true, message: '', tigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '' }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadChannels()

    // 
    // 
    //  id ,
    if (this.$route.query.id) {
      this.loadArticle()
    }
  },
  mounted () {},
  methods: {
    loadChannels () {
      getArticleChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    onPublish (draft = false) {
      // 
      this.$refs['publish-form'].validate(valid => {
        // ,
        if (!valid) {
          return
        }
        // ,

        // 
        // 
        // 
        // 

        // ,
        const articleId = this.$route.query.id
        if (articleId) {
          updateArticle(articleId, this.article, draft).then(res => {
            this.$message({
              message: '',
              type: 'success'
            })
            this.$router.push('/article')
          })
        } else {
          AddArticle(this.article, draft).then(res => {
            console.log(res)
            this.$message({
              message: `${draft ? '' : ''}`,
              type: 'success'
            })
            this.$router.push('/article')
          })
        }
      })
    },
    // 
    loadArticle () {
      // 
      // 
      // 
      // 

      // 
      getArticle(this.$route.query.id).then(res => {
        this.article = res.data.data
      })
    },
    onUpdateCover (index, url) {
      // console.log('onUpdateCover', url)
      this.article.cover.images[index] = url
    }
  }
}
</script>

<style scoped lang="less"></style>
