<template>
  <div class="login-container">
    <div class="login-head">
      <div class="logo"></div>
    </div>
    <!--
       form
      1. el-formmodel
      2. prop
      ; prop
      3. el-form rules
      4.:
       el-form ref 
      ref  el-form, validate
    -->
    <div class="login-form-wrap">
      <el-form ref="login-form" :model="user" class="logo-form" :rules="formRules">
        <el-form-item prop="mobile">
          <el-input v-model="user.mobile" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="code">
          <el-input v-model="user.code" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="agree">
          <el-checkbox v-model="user.agree"></el-checkbox>
        </el-form-item>
        <el-form-item>
          <el-button
            :loading="loginLoading"
            class="login-form-btn"
            type="primary"
            @click="onLogin"
          ></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import { login } from '@/api/user'
export default {
  name: 'LoginIndex',
  data () {
    return {
      user: {
        mobile: '',
        code: '',
        agree: false // 
      },
      loginLoading: false,
      formRules: {
        // 
        // : []
        // trigger  blur 
        mobile: [
          { required: true, message: '', trigger: 'change' },
          {
            pattern: /^1[3|5|7|8|9]\d{9}$/,
            message: '',
            trigger: 'change'
          }
        ],
        code: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^\d{6}$/, message: '' }
        ],
        agree: [
          // 
          // ; callback
          // : callback(new Error('))
          {
            validator: (rule, value, callback) => {
              if (value) {
                callback()
              } else {
                callback(new Error(''))
              }
            },
            trigger: 'change'
          }
        ]
      }
    }
  },
  methods: {
    onLogin () {
      // 
      // validate
      this.$refs['login-form'].validate((valid, err) => {
        console.log(valid)
        console.log(err)
        if (!valid) {
          // 
          return
        }
        // ,
        this.login()
      })
    },
    login () {
      // 
      // const user = this.user

      //  loading...
      this.loginLoading = true

      // 
      // 1.
      // 2. ,, 
      // 
      // : ,
      login(this.user)
        .then(res => {
          // 
          console.log(res)
          this.$message({
            message: '',
            type: 'success'
          })

          // loading
          this.loginLoading = false

          // ,,
          // 
          // ,Json
          window.localStorage.setItem('user', JSON.stringify(res.data.data))

          // 
          // this.$router.push('/')

          // 
          this.$router.push({
            name: 'home'
          })
        })
        .catch(err => {
          // 
          console.log(err)
          this.$message.error(',')

          // loading
          this.loginLoading = false
        })
    }
  }
}
</script>
