<template>
<div class="upload-cover" @click="showCoverSelect">
  <div class="cover-wrap">
    <img class="cover-image" ref="cover-image" :src="value">
  </div>
  <el-dialog
    title=""
    :visible.sync="dialogVisible"
    append-to-body
  >
  <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <!-- ref:
        1. html,DOM
        2. 
      -->
    <el-tab-pane label="" name="first">
      <image-list ref="image-list" :is-show-add='false' :is-show-action='false' is-show-selected></image-list>
    </el-tab-pane>
      <el-tab-pane label="" name="second">
        <input ref="file" type="file" @change="onFileChange">
        <img ref="preview-image" width="100" :src="value">
      </el-tab-pane>
    </el-tabs>
    <span slot="footer" class="dialog-footer">
       <el-button @click="dialogVisible = false">  </el-button>
       <el-button type="primary" @click="onCoverConfirm"> </el-button>
    </span>
  </el-dialog>
</div>
</template>

<script>
import ImageList from '@/views/image/components/image-list'
import { uploadImages } from '@/api/image'
export default {
  name: 'UploadCover',
  components: {
    ImageList
  },
  // props: ['cover-image'],
  props: ['value'],
  data () {
    return {
      dialogVisible: false,
      activeName: 'first'
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    showCoverSelect () {
      this.dialogVisible = true
    },
    handleClick (tab, event) {
      console.log(tab, event)
    },
    // input
    onFileChange () {
      const file = this.$refs.file.files[0]
      const blob = window.URL.createObjectURL(file)
      this.$refs['preview-image'].src = blob
      // 
    //   this.$refs.file.value = ''
    },
    // 
    onCoverConfirm () {
      //  tab ,  input-file ,
      if (this.activeName === 'second') {
        const file = this.$refs.file.files[0]
        if (!file) {
          this.$message('')
          return
        }
        // 
        const fd = new FormData()
        fd.append('image', file)
        uploadImages(fd).then(res => {
          console.log(res)
          // 
          this.dialogVisible = false
          // 
          this.$refs['cover-image'].src = res.data.data.url

          // 
          this.$emit('input', res.data.data.url)
        //   this.$emit('update-cover', res.data.data.url)
        })
      } else if (this.activeName === 'first') {
        // , 
        const imageList = this.$refs['image-list']
        const selected = imageList.selected
        console.log(imageList.selected)
        if (selected === null) {
          this.$message('')
          return
        }
        // 
        this.dialogVisible = false
        // 
        this.$emit('input', imageList.images[selected].url)
      }
    }
  }
}
</script>

<style scoped lang="less">
.cover-wrap {
    width: 120px;
    height: 120px;
    border: 1px solid #ccc;
    .cover-image {
        width: 120px;
        max-width: 100%;
    }
}
</style>
