<template>
<div class="image-list">
   <el-radio-group v-model="collect" @change="loadImages(1)" size="mini" style="margin-bottom:20px">
      <el-radio-button :label="false"></el-radio-button>
      <el-radio-button :label="true"></el-radio-button>
    </el-radio-group>
    <el-button type="success" size="mini" @click="dialogUploadVisible = true" style="float:right" v-if="isShowAdd"></el-button>
    <!--  -->
    <el-row :gutter="10">
      <el-col :lg="4" :md="6" :sm="6" :xs="12" v-for="(img, index) in images" :key="index" class="list">
        <div>
          <div class="mask"  v-if="isShowAction">
            <!-- : buttoni,button -->
            <!--  loading  -->
            <!-- img,,loading, -->
            <el-button type="warning" circle size="mini" :icon="img.is_collected ? 'el-icon-star-on' : 'el-icon-star-off'"  @click="onCollected(img)" :loading="img.loading"></el-button>
            <el-button type="danger" icon="el-icon-delete" circle size="mini" @click="ondeleteImage(img)" :loading="img.loading"></el-button>
            <!--  -->
            <!--  -->
            <!-- <i :class="{'el-icon-star-on': img.is_collected, 'el-icon-star-off': !img.is_collected}" @click="onCollected(img.id, img.is_collected)"></i>
            <i v-if="img.is_collected === false" class="el-icon-star-off" :class="{isCollected: img.is_collected}" @click="onCollected(img.id, img.is_collected)"></i>
            <i v-else class="el-icon-star-on" :class="{isCollected: img.is_collected}" @click="onCollected(img.id, img.is_collected)"></i> -->
            <!-- <i class="el-icon-delete" @click="ondeleteImage(img.id)"></i> -->
          </div>
          <el-image
          style="height: 100px"
          @click.native="selected = index"
          :src="img.url"
          fit="cover">
          </el-image>
          <!--  -->
          <!-- ,,,, -->
          <div class="selected" v-if="isShowSelected && selected === index"></div>
        </div>
      </el-col>
    </el-row>
    <!--  -->
    <!-- ,, :current-page.sync="page"  -->
    <el-pagination
      style="margin-top:20px"
      background
      layout="prev, pager, next"
      :total="totalCount"
      :page-size="pageSize"
      :current-page.sync="page"
      @current-change="onCurrentPage"
    >
    </el-pagination>
  <!--  -->
  <el-dialog
    title=""
    :visible.sync="dialogUploadVisible"
    :append-to-body="true"
    width="30%"
    class="dialog"
  >
    <!-- upload 
         POST
         action,
         headers
    -->
    <el-upload
      class="upload-demo"
      drag
      action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
      :headers="uploadHeaders"
      name="image"
      :show-file-list="false"
      :on-success="onUploadSuccess"
      multiple>
      <i class="el-icon-upload"></i>
      <div class="el-upload__text"><em></em></div>
      <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
    </el-upload>
  </el-dialog>
</div>
</template>

<script>
import { getImages, deleteImages, collectImages } from '@/api/image'
export default {
  name: 'ImageList',
  components: {},
  // prop,,,,demo
  // prop,
  props: {
    isShowAdd: {
      type: Boolean, // 
      default: true // 
    },
    isShowAction: {
      type: Boolean,
      default: true
    },
    isShowSelected: {
      type: Boolean,
      default: false
    }
  },
  data () {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      collect: false,
      images: [],
      dialogUploadVisible: false,
      // 
      uploadHeaders: {
        Authorization: `Bearer ${user.token}`
      },
      page: 1, // 1
      pageSize: 12, // 
      totalCount: 0, // 
      selected: null // 
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadImages(1)
  },
  mounted () {},
  methods: {
    loadImages (page = 1) {
      // ,
      this.page = page
      getImages({
        collect: this.collect,
        page,
        per_page: this.pageSize
      }).then(res => {
        console.log(res)
        const results = res.data.data.results
        // results imgloading
        results.forEach(img => {
          // imgloading
          // loading,  loading 
          img.loading = false
        })
        this.images = results
        this.totalCount = res.data.data.total_count
      })
    },
    // loadImages 
    // oncollectChange (value) {
    //   this.loadImages(value)
    // },
    onUploadSuccess () {
      // 
      this.dialogUploadVisible = false

      // 
      this.loadImages(this.page)
    },
    // 
    onCurrentPage (page) {
      this.loadImages(page)
    },
    // 
    ondeleteImage (img) {
      img.loading = true
      deleteImages(img.id).then(res => {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.loadImages(this.page)
          this.$message({
            type: 'success',
            message: '!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
        img.loading = false
      })
    },
    // 
    // img
    onCollected (img) {
      img.loading = true
      collectImages(img.id, !img.is_collected).then(res => {
        // 
        // img.is_collected = !img.is_collected
        if (img.is_collected === false) {
          this.loadImages(this.page)
          this.$message({
            type: 'success',
            message: '!'
          })

          img.loading = false
        } else {
          this.loadImages(this.page)
          this.$message({
            type: 'success',
            message: '!'
          })

          // 
          img.loading = false
        }
      })
    }
  }
}
</script>

