  position: relative;
  .mask {
    position: absolute;
    bottom: 5px;
    left: 5px;
    right: 5px;
    height: 27px;
    background: rgba(1, 1, 1, .3);
    z-index: 100000;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: yellow;
  }
}
.selected {
    width: 100%;
    background: url(./selected.png) no-repeat;
    background-size: cover;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>
