<template>
<div class="fans-container">
  <el-card class="filter-card">
    <div slot="header" class="clearfix">
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <!-- :to="{name:'home'}"  to='/'-->
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!-- / -->
    </div>
    <!-- tab -->
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="" name="first" @tab-click="loadFans">
        <el-row :gutter="10">
          <el-col :lg="4" :md="6" :sm="6" :xs="12" v-for="(item,index) in fansList" :key="index" style="padding-top:5px">
            <div  class="fans-list">
              <img :src="item.photo" alt="">
              <p>{{ item.name }}</p>
            </div>
          </el-col>
        </el-row>
        <!--  -->
        <el-pagination
            background
            layout="prev, pager, next"
            :page-size="pageSize"
            :total="totalCount"
            :disabled="onDisabled"
            @current-change="loadFans"
            style="margin:20px 0 0 -10px"
        >
        </el-pagination>
      </el-tab-pane>
      <el-tab-pane label="" name="second">
        <!--  -->
        <!-- 1.  ECharts  DOM -->
        <div ref="main" style="width: 600px;height:400px;"></div>
      </el-tab-pane>
    </el-tabs>
  </el-card>
</div>
</template>

<script>
import { getFans } from '@/api/user'
// 2.echarts
import echarts from 'echarts'

export default {
  name: 'FansIndex',
  components: {},
  props: {},
  data () {
    return {
      activeName: 'first',
      fansList: [],
      pageSize: 20, // 
      totalCount: 0, // 
      onDisabled: false // 
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadFans(1)
  },
  // : DOM  mounted 
  mounted () {
    // 3. domecharts
    var myChart = echarts.init(this.$refs.main)

    // 
    var option = {
      title: {
        text: ''
      },
      tooltip: {},
      legend: {
        data: ['']
      },
      xAxis: {
        data: ['', '', '', '', '', '']
      },
      yAxis: {},
      series: [{
        name: '',
        type: 'bar',
        data: [5, 20, 36, 10, 10, 20]
      }]
    }

    // 
    myChart.setOption(option)
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    // 
    loadFans (page = 1) {
      this.onDisabled = true
      getFans({
        page, // ,
        per_page: this.pageSize // 
      }).then(res => {
        console.log(res)
        this.fansList = res.data.data.results
        this.totalCount = res.data.data.total_count
        this.onDisabled = false
      })
    }
  }
}
</script>

