<template>
<div class="article-container">
  <el-card class="filter-card">
    <div slot="header" class="clearfix">
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <!-- :to="{name:'home'}"  to='/'-->
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!-- / -->
    </div>
    <!--  -->
    <el-form ref="form" :model="form" label-width="40px" size="mini">
        <el-form-item label="">
        <el-radio-group v-model="status">
            <!-- radio labelvalue -->
            <!-- lable:  ,  : -->
            <el-radio :label="null"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="4"></el-radio>
        </el-radio-group>
        </el-form-item>
        <el-form-item label="">
        <!--  -->
        <el-select v-model="channelId" placeholder="">
            <!--  -->
            <el-option label="" :value="null"></el-option>
            <!--  -->
            <el-option v-for="(channel,index) in channels" :key="index" :label="channel.name" :value="channel.id"></el-option>
        </el-select>
        </el-form-item>
        <el-form-item label="">
        <el-date-picker
            v-model="rangeDate"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            type="datetimerange"
            start-placeholder=""
            end-placeholder=""
            :default-time="['12:00:00']">
        </el-date-picker>
        </el-form-item>
        <el-form-item>
        <el-button type="primary" :disabled="loading" @click="loadArticles(1)"></el-button>
        </el-form-item>
    </el-form>
    <!-- / -->
  </el-card>

    <el-card class="box-card">
    <div slot="header" class="clearfix">
      {{totalCount}}
    </div>
    <!--  -->
    <!-- Table
        1. table
        data
          :v-for,
        2.el-table-column width
        lable
        prop ,
        3. ,,, -->
    <el-table
      :data="articles"
      stripe
      v-loading="loading"
      class="list-table"
      size="mini"
      style="width: 100%">
      <el-table-column
        label="">
        <template slot-scope="scope">
          <!--  -->
          <!-- <img
            v-if="scope.row.cover.images[0]"
            class="article-cover" :src="scope.row.cover.images[0]" alt="">
          <img v-else class="article-cover" src="./1.jpeg" alt=""> -->
          <!--  -->
          <el-image
            style="width: 60px; height: 60px"
            :src="scope.row.cover.images[0]"
            fit="fit">
            <div slot="placeholder" class="image-slot">
              <span class="dot">...</span>
            </div>
          </el-image>
        </template>
      </el-table-column>
      <el-table-column
        prop="title"
        label="">
      </el-table-column>
      <!-- prop   -->
      <el-table-column
        label="">
        <!-- ,template slot-scope="scope" -->
        <!--   v-if -->
        <!-- slot-scope ,scope  -->
        <!-- row -->
        <template slot-scope="scope">
          <el-tag :type="articleStatus[scope.row.status].type">{{ articleStatus[scope.row.status].text }}</el-tag>
          <!-- <el-tag v-else-if="scope.row.status === 1"></el-tag>
          <el-tag type="success"  v-else-if="scope.row.status === 2"></el-tag>
          <el-tag type="info"  v-else-if="scope.row.status === 3"></el-tag>
          <el-tag type="danger"  v-else-if="scope.row.status === 4"></el-tag> -->
        </template>
      </el-table-column>
      <el-table-column
        prop="pubdate"
        label="">
      </el-table-column>
      <el-table-column
        label="">
        <!-- ,
        template,,,
        ,circle -->
        <!-- toString() -->
        <template slot-scope="scope">
          <el-button
          @click="$router.push('/publish?id='+scope.row.id.toString())"
          size="mini"
          ></el-button>
          <el-button
            size="mini"
            type="danger"
            @click="onDeleteArticle(scope.row.id)"
            ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- / -->
    <!--  -->
    <!-- total,10 -->
    <el-pagination
      background
      layout="prev, pager, next"
      :total="totalCount"
      @current-change="loadArticles"
      :disabled="loading"
      :page-size="pageSize"
      :current-page.sync="page"
    >
    </el-pagination>
    <!-- / -->
  </el-card>
</div>
</template>

<script>
import { getArticles, getArticleChannels, deleteArticle } from '@/api/article'
export default {
  name: 'ArticleIndex',
  components: {},
  props: {},
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      articles: [],
      articleStatus: [
        { status: 0, text: '', type: '' },
        { status: 1, text: '', type: 'info' },
        { status: 2, text: '', type: 'success' },
        { status: 3, text: '', type: 'warning' },
        { status: 4, text: '', type: 'danger' }
      ],
      totalCount: 0, // 
      pageSize: 20,
      // axios  null, undefined 
      status: null, // ,
      loading: true,
      page: 1,
      channels: [], // 
      channelId: null,
      rangeDate: null
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadArticleChannels()
    this.loadArticles()
  },
  mounted () {},
  methods: {
    loadArticles (page = 1) {
      // 
      this.loading = true
      getArticles({
        page,
        per_page: this.pageSize,
        status: this.status,
        channel_id: this.channelId,
        begin_pubdate: this.rangeDate ? this.rangeDate[0] : null, // 
        end_pubdate: this.rangeDate ? this.rangeDate[1] : null // 
      }).then(res => {
        // 
        // total_count: totalCount ES6,
        const { results, total_count: totalCount } = res.data.data
        this.articles = results
        this.totalCount = totalCount

        // loading
        this.loading = false
      })
    },
    // ,  loadArticles() 
    // @current-change="oncurrentchange"  loadArticles()
    // oncurrentchange (page) {
    //   this.loadArticles(page)
    // },
    // 
    loadArticleChannels () {
      getArticleChannels().then(res => {
        console.log(res)
        this.channels = res.data.data.channels
      })
    },
    // 
    onDeleteArticle (articleId) {
      // 
      // 
      // 
      // 
      // js-2^532^53  
      // json-bigint 
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteArticle(articleId.toString()).then(res => {
          //   
          this.loadArticles(this.page)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
