<template>
<el-container class="layout-container">
  <el-aside class="aside" width="auto">
    <app-aside class="aside-menu" :isCollapse = 'isCollapse'></app-aside>
  </el-aside>
  <el-container>
    <el-header class="header">
      <div>
        <i :class="{'el-icon-s-fold': isCollapse, 'el-icon-s-unfold': !isCollapse}" @click="isCollapse = !isCollapse"></i>
        <span></span>
      </div>
      <el-dropdown>
        <div class="avatar-wrap">
            <img class="avatar" :src="user.photo">
            <span>{{ user.name }}</span>
        </div>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item></el-dropdown-item>
        <!-- , -->
        <!-- native -->
        <el-dropdown-item @click.native="onLogout()"></el-dropdown-item>
      </el-dropdown-menu>
      </el-dropdown>
      </el-header>
          <el-main class="main">
            <router-view></router-view>
          </el-main>
  </el-container>
</el-container>
<!-- <div class="layout-container"> -->
    <!-- <div></div>

    <div></div> -->
     <!--  -->
     <!--  -->
